.aboutMe {
  width: 100%;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(1, 10, 141, 0.616);
  box-sizing: border-box;
  overflow-y: scroll;
}

.miniHeader {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(1, 10, 141);
  border-bottom: 1px solid white;
  position: sticky;
  top: 0;
}

.btnLink {
  font-size: 0.8rem;
  font-weight: 500;
  color: white;
  background-color: rgb(0, 0, 87);
  text-decoration: none;
  border-radius: 6px;
  padding: 5px 20px;
  margin: 5px 0;
}

.btnLink:hover {
  color: rgb(202, 198, 198);
  background-color: rgb(4, 4, 194);
  text-decoration: none;
  border-radius: 6px;
  padding: 5px 20px;
  margin-top: 5px;
}

.aboutMeContent {
  width: 90%;
  height: 100%;
  padding: 30px 0;
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  box-sizing: border-box;
}

.aboutMeContent p {
  font-size: 0.8rem;
  line-height: 1.7em;
  margin-bottom: 15px;
}

footer {
  width: 100%;
  text-align: center;
  color: white;
  border-top: 1px solid white;
  padding: 10px 0;
  font-size: 0.8rem;
}

.dropCap::first-letter {
  float: left;
  font-size: 5em;
  font-family: Montserrat;
  padding-top: 12px;
  padding-bottom: 5px;
  margin-right: 5px;
  margin-bottom: -5px;
}

@media screen and (min-width: 720px) {
  .aboutMe {
    width: 100%;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(1, 10, 141, 0.616);
    box-sizing: border-box;
    overflow-y: scroll;
  }

  .miniHeader {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid white;
    background-color: rgba(1, 10, 141);
    position: sticky;
    top: 0;
  }

  .btnLink {
    color: white;
    background-color: rgb(0, 0, 87);
    text-decoration: none;
    border-radius: 6px;
    padding: 5px 20px;
    margin: 5px 0;
  }

  .btnLink:hover {
    color: rgb(202, 198, 198);
    background-color: rgb(4, 4, 194);
    text-decoration: none;
    border-radius: 6px;
    padding: 5px 20px;
    margin-top: 5px;
  }

  .aboutMeContent {
    width: 80%;
    height: 100%;
    padding: 30px 0;
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    box-sizing: border-box;
  }

  .aboutMeContent p {
    font-size: 1.1em;
    line-height: 1.7em;
    margin-bottom: 15px;
  }

  footer {
    width: 100%;
    text-align: center;
    color: white;
    border-top: 1px solid white;
    padding: 10px 0;
    font-size: 0.8rem;
  }
}
